.middle-new {
	width: 37.5vw;
	height: 100vh;
	padding-left: 3%;
	padding-right: 1%;
	padding-top: 5%;
	display: flex;
	flex-direction: column;
	justify-content: left;
	align-items: left;
	/* background-color: red; */
}

.middle-new .title {
	font-weight: 600;
	font-size: 28px;
	line-height: 128.5%;
	/* identical to box height, or 36px */

	letter-spacing: 0.02em;
	padding-left: 1rem;

	color: #f7f7fc;
}

.right-new {
	width: 37.5vw;
	height: 100vh;
	padding-left: 1%;
	padding-right: 4%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* background-color: blue; */
}
