@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
	margin: 0;
	padding: 0;
	height: 100vh;
	width: 100vw;
	font-family: "poppins", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #121212;
	color: #ffffff;
	box-sizing: border-box;
	/* overflow-y: hidden; */
	overflow-x: hidden;
}

::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: transparent;
}

::-webkit-scrollbar {
	width: 3px;
	background-color: transparent;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #555;
}

@font-face {
	font-family: "MonumentRegular";
	src: local("MonumentRegular"),
		url(/evo-admin/static/media/MonumentExtended-Ultrabold.9db16afe.otf) format("otf");
}
@font-face {
	font-family: "MonumentBold";
	src: local("MonumentBold"),
		url(/evo-admin/static/media/MonumentExtended-Regular.db74f7a5.otf) format("otf");
}

.left {
	width: 23vw;
	height: 100vh;
	padding-left: 2%;
	padding-right: 0%;
	/* display: flex;
  justify-content: center;
  align-items: center; */
	/* background-color: blue; */
	margin: 0;
	box-sizing: border-box;
}

.layout {
	height: 100vh;
	width: 100vw;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-image: url(/evo-admin/static/media/bg.f40dca96.png);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.logo-img {
	width: 8%;
	height: auto;
	pointer-events: none;
}

.logo-text {
	font-style: italic;
	font-weight: bold;
	font-size: 6.75em;
	line-height: 75px;
	text-align: center;
	letter-spacing: 0.02em;
	color: #ffffff;
	width: 22%;
	margin-bottom: 2%;
	margin-top: 2%;
}
.sub-text {
	font-family: MonumentRegular;
	font-style: normal;
	font-weight: 500;
	font-size: 2.25em;
	line-height: 42px;
	text-align: center;
	letter-spacing: 0.02em;
	color: #ffffff;
	margin-top: 0.3%;
}

.black {
	position: absolute;
	z-index: 50;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100vw;
	background-color: #000000;
	overflow: hidden;
}
.black .overflow-hidden {
	overflow: hidden;
}

.black-text {
	position: absolute;
	z-index: 50;
	display: flex;
}

.text-white {
	color: #7d50ff;
}

.rectangle {
	width: 100vw;
	height: 100vw;
	fill: currentColor;
}
.rectangle-text {
	width: 100vw;
	height: 100vh;
	color: #ffffff;
	font-weight: 600;
	fill: currentColor;
}

.rect-text {
	font-weight: bold;
	font-size: 2.25rem;
	line-height: 2.5rem;
	font-family: poppins;
}

/* Buttons CSS */
.or {
	margin: 7px;
}
.btn-main {
	margin-top: 4%;
	width: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
button {
	opacity: 0.9;
	transition: 0.5s linear;
}
button:hover {
	opacity: 1;
}

/* Galaxy S5 */
@media only screen and (max-width: 400px) {
	.logo-img {
		width: 25%;
	}
	.logo-text {
		font-size: 2.5em;
	}
	.sub-text {
		font-size: 1em;
	}
}
/* Pixel 2 or Pixel 2 XL */
@media only screen and (min-width: 401px) and (max-width: 576px) {
	.logo-img {
		width: 25%;
	}
	.logo-text {
		font-size: 2.5em;
	}
	.sub-text {
		font-size: 1em;
	}
}
/* iPad */
@media only screen and (min-width: 577px) and (max-width: 768px) {
	.logo-img {
		width: 20%;
	}
	.logo-text {
		font-size: 3.6em;
	}
	.sub-text {
		font-size: 1.8em;
	}
}
/* iPad Pro */
@media only screen and (min-width: 769px) and (max-width: 990px) {
	.logo-img {
		width: 15%;
	}
	.logo-text {
		font-size: 2.5em;
	}
	.sub-text {
		font-size: 1em;
	}
}
/* 1280 x 900 */
@media only screen and (min-width: 991px) and (max-width: 1280px) {
	.logo-img {
		width: 20%;
	}
	.logo-text {
		font-size: 3.6em;
	}
	.sub-text {
		font-size: 1.8em;
	}
}

.main {
	height: 100vh;
	width: 100vw;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: row;
}
.left-login {
	width: 50vw;
	height: 100vh;
	background-image: url(/evo-admin/static/media/log-bg.eb60832f.png);
	background-repeat: no-repeat;
	padding: 5%;
