}

.logo-login {
	width: 105px;
	height: auto;
	margin-bottom: 5%;
}

.logo-login-text {
	font-family: Raleway;
	font-style: italic;
	font-weight: 800;
	font-size: 64px;
	line-height: 75px;
	letter-spacing: 0.02em;
	color: #dcd0ff;
	margin: 0;
	border-bottom: 3px solid #dcd0ff;
