}
.middle-card p {
	font-weight: 600;
	/* font-size: 21px; */
	line-height: 31px;
}
.upcoming-icon {
	height: 90px;
	width: 90px;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	padding-left: 5%;
	padding-right: 5%;
