}
.sign-detail img {
	padding-right: 5%;
}

/* 1280,990,768,576 and 400 */

.left-nav {
	width: 20vw;
	height: 100vh;
	padding-left: 2%;
	padding-right: 0%;
	position: absolute;
	z-index: 99;
	display: flex;
	justify-content: center;
	align-items: center;
