	margin: 0;
	overflow: hidden;
	/* background-color: royalblue; */
}

.nav-main {
	background-color: #181818;
	border-radius: 32px;
	height: 95%;
	width: 85%;
	padding-left: 7%;
	padding-right: 7%;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: stretch;
	overflow: hidden;
}

.nav-logo {
	width: 100%;
	flex-grow: 1;
