.nav-logo .nav-text .event-count {
	font-weight: 600;
	font-size: 0.78em;
	line-height: 1.125em;
	color: rgba(255, 255, 255, 0.65);
	margin: 0;
	text-overflow: ellipsis;
}

.nav-menu {
	width: 100%;
	flex-grow: 1.5;
	/* height: 28.6%; */
